#appbar{
width: 100%;
margin-left: -1px;
}
#rayan{
cursor: pointer;
font-family: Arial, Helvetica, sans-serif;
font-size: 25px;
font-weight: bold;
}
#btn{
    position: fixed;
    display: none;
}

#btn_nav_S{
display: block;
color: black;
}
#icon,#close{
font-size: 25px;
left: 81%;
top: 25%;
cursor: pointer;
position: absolute;
display: none;
}
#close{
    display: none;
}
#text{
    font-size: 90px;
    font-family: Arial, Helvetica, sans-serif;
    margin-left: 5%;
   
}
#ray{
    font-size: 72px;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
}
@media (max-width:500px){
    #icon{
        display: block;
    }
    #btn_nav_S{
        display: none;
    }
    #btn_nav_Q{
       height: 50px;
       width: 105px;
       font-size: 20px;
       color: red;
       border: 1px solid red;
       margin-bottom: 10px;
      
    }
    #btn{ 
        z-index: 2000;
        position: fixed;
        top: 10%;
        left: 45%;
       
    }
}

